<template>
  <div class="container">
    <form @submit.prevent="handleSubmit" v-if="showForm">
      <label for="city">City:</label>
      <input type="text" v-model="city" required />
      <label for="state">State:</label>
      <input type="text" v-model="state" />
      <div class="center">
        <button class="btn">Search</button>
      </div>
    </form>
    <div v-else>
      <div class="center">
        <h2>{{ response.name }},{{ response.sys.country }}</h2>
      </div>
      <h4>
        Temprature:
        <span class="inner__h3"
          >{{ kavTodegC(response.main.temp).toFixed(2) }} &deg;C</span
        >
      </h4>
      <h4>
        Humidity:
        <span class="inner__h3">{{ response.main.humidity }} %</span>
      </h4>
      <h4>
        Latitude:
        <span class="inner__h3">{{ response.coord.lat }} &deg;E</span>
      </h4>
      <h4>
        Longitude:
        <span class="inner__h3">{{ response.coord.lon }} &deg;N</span>
      </h4>
      <h4>
        Visibility:
        <span class="inner__h3"
          >{{ parseInt(response.visibility) / 1000 }}Km</span
        >
      </h4>
      <h4>
        Description:
        <span class="inner__h3">{{ response.weather[0].main }}</span>
      </h4>
      <div class="center">
        <button class="btn" @click="toggleShowForm">Back</button>
      </div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";

export default {
  name: "Main",
  data() {
    return {
      city: "Delhi",
      state: "",
      showForm: true,
      response: "",
    };
  },
  methods: {
    async handleSubmit() {
      await fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=${this.city},${this.state},IN&appid=af7af0e3303122a57a76aeb4557f1603`
      )
        .then((res) => res.json())
        .then((data) => {
          this.toggleShowForm();
          this.response = data;
        });
    },
    toggleShowForm() {
      this.showForm = !this.showForm;
    },
    kavTodegC(temp) {
      if (typeof temp === "number") {
        return temp - 273.15;
      } else {
        return parseInt(temp) - 273.15;
      }
    },
  },
};
</script>

